<template>
    <div calss="login">
        <div style="padding:25px;letter-spacing:5px;">
            <router-link to="/" @click.native="fromShow">登入</router-link>&nbsp;
            <router-link to="/from" @click.native="fromShow">帳戶增刪查</router-link>
        </div>
        <div v-if="fromshow">
            <router-view></router-view>
        </div>
        <div v-else>
            帳號:<input v-model="username"/>
            密碼:<input type="password" v-model="password"/>
            <button @click="login">登入</button>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    name:'login',
    data(){
        return{
            username:'',
            password:'',
            fromshow: true,
            respdata:[]
        }
    },
    methods:{
        login(){
            axios
            .post("http://127.0.0.1:8081/login",
            this.$qs.stringify({
                user:this.username,
                pwd:this.password
            })
            )
            .then(response=>{
                this.respdata=response.data
                console.log(this.respdata)
                if(this.respdata=="無此帳號"||this.respdata=="密碼錯誤"){
                    alert("帳號密碼錯誤")
                }else{
                    this.$router.push({name:"user",params:{name:this.username,sessionid:this.respdata}})
                }    
                })
            .catch(error=>error)
        },
        fromShow(){
            this.fromshow= !this.fromshow
        }
    }
    
}
</script>